import React, { useEffect, useState, useCallback } from 'react';
import './Blog.scss';
import posts from '../../assets/image.png';
import BlogCard from '../BlogCard/BlogCard';
import Pagination from '../Pagination/Pagination';
import { useMatchMedia } from '../../hook/useMatchMedia';
import axios from 'axios';

const Blog = () => {
  const [currentPage, setCurrentPage] = useState(1);
  const [blogPerPage, setBlogPerPage] = useState(3);
  const [cards, setCards] = useState([
    {
      id: 1,
      image: posts,
      time: '15.12.2021',
      likes: ['a', 'b'],
      dislikes: [],
      title: '     ?',
      text: '          ,    .       , , .        .           .          ..          ,    .   ,     !',
    },
    {
      id: 2,
      image: posts,
      likes: ['a', 'b', 'c'],
      dislikes: ['d'],
      time: '27.05.2021',
      title: ' ',
      text: '             , :     ,       -  ,           .',
    },
    {
      id: 3,
      image: posts,
      time: '32423',
      likes: ['gh', 'sdf', 'sghg', 'dfhg', 'dfh'],
      dislikes: ['sdjg'],
      title: '',
      text: '               ,  ,    .         ,     ,  ,      ,     [1].            .:   ,   ,    -  ,           [2].             ,       .        ,  ,   .   ( . receptio - ) 1)              .',
    },
    {
      id: 4,
      image: posts,
      time: '143551',
      likes: [],
      dislikes: [],
      title: '43535 ?',
      text: ' 345356637     ,    .       , , .        .           .          ..          ,    .   ,     !',
    },
    {
      id: 5,
      image: posts,
      time: '346486856',
      likes: [],
      dislikes: [],
      title: '4365',
      text: ' 12415        , :     ,       -  ,           .',
    },
    {
      id: 6,
      image: posts,
      time: '3rty',
      likes: [],
      dislikes: [],
      title: 'eeee',
      text: ':     ,       -  ,           .',
    },
  ]);
  const isDesktopResolution = useMatchMedia('(max-width:1050px)', true);
  // const getCards = useCallback(() => {
  //   axios
  //     .get('http://localhost:7000/api/blogs')
  //     .then(res => setCards(res.data));
  // }, [cards]);

  useEffect(() => {
    !isDesktopResolution ? setBlogPerPage(3) : setBlogPerPage(2);
  }, [isDesktopResolution]);

  useEffect(() => {
    // getCards();
  }, []);

  const lastIndex = currentPage * blogPerPage;
  const firstIndex = lastIndex - blogPerPage;
  const currentBlogs = cards.slice(firstIndex, lastIndex);
  return (
    <section id='blog' className='blog'>
      <h2 className='title blog__title'></h2>
      <div className='blog__container'>
        {currentBlogs &&
          currentBlogs.map((card) => <BlogCard key={card.id} {...card} />)}
      </div>
      <Pagination
        blogPerPage={blogPerPage}
        totalBlog={cards.length}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />
    </section>
  );
};

export default Blog;
